import * as THREE from "three";
import { Vector3 } from "three";

var sin = Math.sin;
var cos = Math.cos;
// var tan = Math.tan;
// var sinh = Math.sinh;
// var cosh = Math.cosh;
var pi = Math.PI;

function VectorCyl(rho, phi, z) {
  var x = rho * cos(phi);
  var y = rho * sin(phi);
  return new THREE.Vector3(x, y, z);
}

// function VectorSph(r, theta, phi) {
