//   udot += uAccel(u, v, udot, vdot);
//   vdot += vAccel(u, v, udot, vdot);
//   u += udot;
//   v += vdot;
//   setCameraStuff(u, v, udot, vdot);
//   render();
// }

renderer.setAnimationLoop(() => {
  udot += uAccel(u, v, udot, vdot);
  vdot += vAccel(u, v, udot, vdot);
  u += udot;
  v += vdot;
  setCameraStuff(u, v, udot, vdot);
  renderer.render(scene, camera);
});

